/* quicksand-regular - latin-ext_latin */
@font-face {
    font-family: 'Quicksand';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('./quicksand/quicksand-v24-latin-ext_latin-regular.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('./quicksand/quicksand-v24-latin-ext_latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./quicksand/quicksand-v24-latin-ext_latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('./quicksand/quicksand-v24-latin-ext_latin-regular.woff') format('woff'), /* Modern Browsers */
         url('./quicksand/quicksand-v24-latin-ext_latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('./quicksand/quicksand-v24-latin-ext_latin-regular.svg#Quicksand') format('svg'); /* Legacy iOS */
  }
